
import { defineComponent } from "vue";
import MainDeck from "@/components/DeckKind/MainDeck.vue";
import ExtraDeck from "@/components/DeckKind/ExtraDeck.vue";
import {selectedDeckGlobal,decks, deck } from "@/components/global"
import { Card, CardType, Deck, getDecks, getDeck, setDeck, setDecks } from "@/API";
export default defineComponent({
  components: { MainDeck,ExtraDeck },
  mounted() {
    if (getDeck()) {
      this.selectedDeck = getDeck().name;
    }
  },
  setup(){
    return{selectedDeckGlobal, decks, deck};
  },
  data() {
    return {
      selectedDeckKind: "mainDeck",
      selectedDeck:"",
    };
  },
  methods: {
    loadDeck() {
     
      if (this.selectedDeck) {
        if(!this.decks){
          this.decks=[]
           this.deck =
           {
            name:this.selectedDeck,
            cards:[],
            extraCards: [],
            combos: [],
            cardGroups: [],
          }
          this.decks.push(this.deck)
        }else{
        if (
          this.decks[this.decks.findIndex((d) => d.name == this.selectedDeck)]
        ) {
          this.deck =
            this.decks[
              this.decks.findIndex((d) => d.name == this.selectedDeck)
            ];
        } else {
          this.decks.push({
            name: this.selectedDeck,
            extraCards:[],
            cards: [],
            combos: [],
            cardGroups: [],
          });
          this.deck =
            this.decks[
              this.decks.findIndex((d) => d.name == this.selectedDeck)
            ];
        }
        }
        this.safeDeck()
      }
    },
        safeDeck() {
      this.decks[this.decks.findIndex((d) => d.name == this.deck.name)] =
        this.deck;
      setDeck(this.deck);
      setDecks(this.decks);
      this.selectedDeckGlobal=this.selectedDeck
    },
  },
});
